// we use JSON.parse here instead of just loading it as a javascript object literal
// because according to https://v8.dev/blog/cost-of-javascript-2019#json that is faster
mergeI18nTranslations(JSON.parse("{\"ar\":{\"done_8af51c9b\":\"انتهاء.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"سيتم إعادة توجيهك إلى دفتر الدرجات بينما يتم تحميل ملفك. إذا كان لديك ملف CSV كبير، فقد تستغرق تحديث تغييراتك بضع دقائق. لمنع استبدال أي بيانات، يُرجى التأكد من اكتمال التحميل وأن دفتر الدرجات صحيح قبل إجراء تغييرات إضافية.\"},\"cy\":{\"done_8af51c9b\":\"Wedi gorffen.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Byddwch chi’n cael eich ailgyfeirio at Lyfrau Graddau wrth i'ch ffeil gael ei llwytho i fyny. Os oes gennych chi ffeil CSV fawr, mae’n bosib y bydd yn cymryd ychydig o funudau i ddiweddaru’ch newidiadau. I sicrhau na fydd unrhyw ddata’n cael eu disodli, cadarnhewch fod y broses llwytho i fyny wedi gorffen a bod y Llyfr Graddau yn gywir cyn gwneud unrhyw newidiadau ychwanegol.\"},\"da\":{\"done_8af51c9b\":\"Færdig.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Du vil blive omdirigeret til Karakteroversigt mens din fil bliver uploadet. Hvis du har en stor CSV-fil, kan kan det tage et par minutter at opdatere ændringerne. For at undgå at overskrive data skal du bekræfte, at overførslen er afsluttet og at Karakteroversigten er korrekt, før du foretager yderligere ændringer.\"},\"da-x-k12\":{\"done_8af51c9b\":\"Færdig.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Du vil blive omdirigeret til vurderingsoversigten mens din fil bliver uploadet. Hvis du har en stor CSV-fil, kan kan det tage et par minutter at opdatere ændringerne. For at undgå at overskrive data skal du bekræfte, at overførslen er afsluttet og at vurderingsoversigten er korrekt, før du foretager yderligere ændringer.\"},\"de\":{\"done_8af51c9b\":\"Fertig.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Sie werden nach „Notenbuch“ weitergeleitet, während Ihre Datei hochgeladen wird. Bei einer großen CSV-Datei, kann die Übernahme Ihrer Änderungen ein paar Minuten dauern. Um das Überschreiben von Daten zu verhindern, bestätigen Sie bitte, dass das Hochladen beendet und das „Notenbuch“ korrekt ist, bevor Sie weitere Änderungen vornehmen.\"},\"el\":{\"done_8af51c9b\":\"Ολοκληρώθηκε.\"},\"en-AU\":{\"done_8af51c9b\":\"Done.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"You will be redirected to Gradebook while your file is being uploaded. If you have a large CSV file, your changes may take a few minutes to update. To prevent overwriting any data, please confirm the upload has completed and Gradebook is correct before making additional changes.\"},\"en-CA\":{\"done_8af51c9b\":\"Done.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"You will be redirected to Gradebook while your file is being uploaded. If you have a large CSV file, your changes may take a few minutes to update. To prevent overwriting any data, please confirm the upload has completed and Gradebook is correct before making additional changes.\"},\"en-GB\":{\"done_8af51c9b\":\"Completed.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"You will be redirected to Gradebook while your file is being uploaded. If you have a large CSV file, your changes may take a few minutes to update. To prevent overwriting any data, please confirm the upload has completed and Gradebook is correct before making additional changes.\"},\"es\":{\"done_8af51c9b\":\"Listo.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Será redireccionado al Libro de calificaciones mientras se carga su archivo. Si tiene un archivo CSV de gran tamaño, sus cambios pueden tomar unos minutos en actualizarse. Para prevenir la sobreescritura de datos, confirme que se ha completado la carga y la libreta de calificaciones es correcta antes de realizar cambios adicionales.\"},\"fa\":{\"done_8af51c9b\":\"انجام شد\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"وقتی فایل‌های شما بارگذاری می‌شود، به دفتر نمره هدایت می‌شوید. اگر یک فایل CSV بزرگ داشته باشید ممکن است بهروزرسانی تغییرات شما چند دقیقه طول بکشد. برای جلوگیری از دوباره‌نویسی هرگونه داده، لطفا قبل از ایجاد هر گونه تغییر، کامل شدن بارگذاری و صحت دفتر نمره را تأیید کنید.\"},\"fi\":{\"done_8af51c9b\":\"Vlms.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Sinut ohjataan arvosanakirjaan, kun tiedostoa ladataan. Jos sinulla on laaja CSV-tiedosto, muutoksien lataus saattaa kestää muutaman minuutin. Jotta estävät tietojen päällekirjoittamisen, vahvista, että lataus on valmis ja arvosanakirja on oikein ennen kuin teet lisämuutoksia.\"},\"fr\":{\"done_8af51c9b\":\"Terminé.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Vous serez redirigé vers le Carnet de notes pendant que votre fichier sera en cours d'envoi. Si vous avez un gros fichier CSV, vos changements ne seront pris en compte qu'au bout de quelques minutes. Pour empêcher tout écrasement de données, veuillez vous assurer que l'envoi est terminé et que le Carnet de note est correct avant d'effectuer de nouveaux changements.\"},\"fr-CA\":{\"done_8af51c9b\":\"Terminé.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Vous allez être redirigé vers le carnet de notes pendant que votre fichier est en train d’être téléversé. Si vous avez un gros fichier CSV, vos modifications peuvent prendre quelques minutes avant d’être mises à jour. Afin d’éviter l’écrasement des données, veuillez confirmer que le téléversement s’est effectué et que le carnet de notes est exact avant de faire d’autres modifications.\"},\"he\":{\"done_8af51c9b\":\"בוצע.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"בעת שהקובץ שלך מועלה כעת תנותב/י לספר הציונים. אם מדובר בקובץ CSV גדול, עדכון השינויים יכול להמשך כמה דקות. כדי למנוע דריסת נתונים כלשהם, בבקשה לוודא שההעלאה הושלמה וספר הציונים שלך נכון, לפני המשך ביצוע שינויים נוספים. \"},\"ht\":{\"done_8af51c9b\":\"Fini.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Y ap redirije w nan Bilten pandan fichye ou a ap transfere. Si w gen yon gwo fichye CVS, chanjmnan ouo yo ka pran tit an pou yo parèt. Pou evite ranplasman done, tanpri konfime ke transfè a fini epi Bilten la pa gen pwoblèm anvan w fè lòt chanjman.\"},\"hu\":{\"done_8af51c9b\":\"Kész.\"},\"hy\":{\"done_8af51c9b\":\"Պատրաստ է:\"},\"is\":{\"done_8af51c9b\":\"Lokið.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Þú verður færð/ur yfir í Einkunnabók meðan verið er að hlaða skrá þinni upp. Sé CSV-skráin stór gætu breytingar þínar tekið nokkrar mínútur í að uppfæra sig. Til að koma í veg fyrir að þú skrifir yfir gögn skaltu fyrst staðfesta að upphleðslunni sé lokið og að einkunnabókin sé rétt áður en þú gerir einhverjar breytingar.\"},\"it\":{\"done_8af51c9b\":\"Fatto.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Sarai reindirizzato al registro dei voti durante il caricamento del tuo file. Se hai un file CSV di grandi dimensioni, l'aggiornamento delle tue modifiche potrebbe richiedere alcuni minuti. Per evitare di sovrascrivere i dati, conferma che il caricamento è stato completato e che il registro dei voti è corretto prima di apportare modifiche aggiuntive.\"},\"ja\":{\"done_8af51c9b\":\"終了です。\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"ファイルがアップロードされている間、グレードブックにリダイレクトされます。CSV ファイルが大きいと、変更が更新されるまでに数分かかる場合があります。データを上書きを防ぐため、アップロードが完了しており、グレードブックが正しいことを確認してから追加の変更を行ってください。\"},\"mi\":{\"done_8af51c9b\":\"Kua mahia.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Ka toroatia koe ki te Pukakoeke i te wā e tikiake ana tō konae. Mehemea he nui to CSV kōnae, ka mahia ō huringa i te roanga o ngā meneti ki te whakahou. Ki te aukati te tuhi anō o ngā raraunga, tēnā koa whakatau i te tikiake kua oti mete Pukakōeke kei te tika i mua o te mahinga huringa atu.\"},\"nb\":{\"done_8af51c9b\":\"Ferdig.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Du vil nå sendes videre til karakterboken mens filen lastes opp. Hvis du har en stor CSV-fil, kan det ta noen minutter å oppdatere. For å unngå at data overskrives må du bekrefte at opplastingen er fullført og at karakterboken er riktig før du gjør ytterligere endringer.\"},\"nb-x-k12\":{\"done_8af51c9b\":\"Ferdig.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Du vil nå sendes videre til vurderingsoversikten mens filen lastes opp. Hvis du har en stor CSV-fil, kan det ta noen minutter å oppdatere. For å unngå at data overskrives må du bekrefte at opplastingen er fullført og at vurderingsoversikten er riktig før du gjør ytterligere endringer.\"},\"nl\":{\"done_8af51c9b\":\"Gereed.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Je wordt doorgestuurd naar Cijferlijst terwijl je bestand wordt geüpload. Als het CSV-bestand erg groot is, kan het enkele minuten duren voordat het bestand is bijgewerkt. Om te voorkomen dat gegevens worden overschreven, moet je bevestigen dat de upload is voltooid en dat de cijferlijst correct is voordat je andere wijzigingen aanbrengt.\"},\"nn\":{\"done_8af51c9b\":\"Fullført.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Du vert omdirigert til vurderingsoversikta medan fila vert lasta opp. Om du har ei stor CSV-fil, kan det ta nokre minutt å oppdatere endringane. Om du vil hindre overskriving av data, må du stadfeste at opplastinga er fullført og vurderingsoversikta er korrekt før du utfører fleire endringar.\"},\"pl\":{\"done_8af51c9b\":\"Gotowe.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"W czasie, gdy plik jest przysyłany, nastąpi przeniesienie do Dziennika ocen. Plik CSV ma duży rozmiar, aktualizacja zmian może zająć kilka minut. Przed wykonaniem dodatkowych zmian, upewnij się, że przesyłanie zostało ukończone, a dziennik jest poprawny, co pomoże zapobiec nadpisaniu danych.\"},\"pt\":{\"done_8af51c9b\":\"Terminado.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Você será redirecionado para o Livro de notas enquanto seu ficheiro está a carregar. Se você tiver um ficheiro CSV grande, suas alterações podem levar alguns minutos para serem atualizadas. Para evitar a substituição de dados, confirme que o carregamento foi concluído e o Livro de notas está correto antes de fazer alterações adicionais.\"},\"pt-BR\":{\"done_8af51c9b\":\"Concluído(a).\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Você será redirecionado para o Boletim enquanto o seu arquivo está sendo carregando. Se você tiver um arquivo CSV grande, as suas alterações podem levar alguns minutos para serem atualizadas. Para evitar a substituição de quaisquer dados, por favor, confirme que o carregamento foi concluído e o seu boletim está correto antes de fazer alterações adicionais.\"},\"ru\":{\"done_8af51c9b\":\"Готово.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Пока идет процесс отправки вашего файла, вы будете перенаправлены в Журнал успеваемости. Если ваш файл CSV имеет большие размеры, для внесения изменений может потребоваться несколько минут. Чтобы предотвратить перезапись любых данных, пожалуйста, подтвердите завершение процесса отправки и правильность заполнения Журнала успеваемости, прежде чем вносить дополнительные изменения.\"},\"sl\":{\"done_8af51c9b\":\"Dokončano.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Med nalaganjem datoteke boste preusmerjeni v redovalnico. Če je datoteka CSV velika, lahko posodobitev sprememb traja nekaj minut. Da preprečite prepisovanje kakršnih koli podatkov, pred dodatnim spreminjanjem potrdite, da je nalaganje zaključeno in da je redovalnica pravilna.\"},\"sv\":{\"done_8af51c9b\":\"Klar.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Du kommer att omdirigeras till bedömningsöversikten medan filen laddas upp. Om du har en stor CSV-fil, kan dina ändringar ta några minuter att uppdatera. För att undvika att skriva över alla data, bekräfta att uppladdningen har slutförts och att bedömningsöversikten är korrekt innan du gör ytterligare ändringar.\"},\"sv-x-k12\":{\"done_8af51c9b\":\"Klar.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Du kommer att omdirigeras till bedömningsöversikten medan filen laddas upp. Om du har en stor CSV-fil, kan dina ändringar ta några minuter att uppdatera. För att undvika att skriva över alla data, bekräfta att uppladdningen har slutförts och att bedömningsöversikten är korrekt innan du gör ytterligare ändringar.\"},\"tr\":{\"done_8af51c9b\":\"Tamam\"},\"uk\":{\"done_8af51c9b\":\"Готово.\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"Ви будете перенаправлені до Gradebook під час завантаження вашого файлу. Якщо у вас великий файл CSV, зміни можуть зайняти кілька хвилин для оновлення. Щоб запобігти перезаписуванню будь-яких даних, будь ласка, підтвердьте, що завантаження завершено, і Gradebook корректний, перш ніж внести додаткові зміни.\"},\"zh-Hans\":{\"done_8af51c9b\":\"完成\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"文件正在上传时，您将重定向至成绩单。如果您的CSV文件较大，您的更改可能需要几分钟才能更新。为了防止覆盖任何数据，请确认上传已完成，且成绩单正确无误，然后再进行其他更改。\"},\"zh-Hant\":{\"done_8af51c9b\":\"完成。\",\"you_will_be_redirected_to_gradebook_while_your_fil_23fab7de\":\"上傳檔案期間，您將回到成績單頁面。如 CSV 檔案過大，或需數分鐘時間才可完成更新。為免覆寫任何資料，請在進行其他變更前，確認上傳是否完成妥當，以及成績單是否正確無誤。\"}}"));
